*{
    margin: 0px;
    padding: 0px;

    --primary-color1:#3f82b1;
    --primary-color2:#484848;
    --primary-color3:#c6e7f1;
    --secondary-color1:#9a9a9a;
    --secondary-color2:#83a3b3;
    --secondary-color3:#ffffff;
    --tertiary-color1:#5c6569;
    --tertiary-color2:#506771;
    --tetriary-color3:#668491;
}


.hjem{
  width: 100%;
  position: absolute;
}
  
.exp-sec{
  position: absolute;
  top: 72px;
  width: 100%;
}

.p1{
  height: 700px;
  width: 100%;
  position: absolute;
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(2, 1fr);
  background:linear-gradient(to bottom, var(--tertiary-color1), var(--tertiary-color1), var(--tertiary-color1), white)
}

.p2{
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 780px;
  width: 100%;
  height: 550px;
}

.p3{
  position: absolute;
  height: 650px;
  width: 100%;
  top: 1370px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.exp-anim{
  position: absolute;
  grid-row: 1 / span 1;
  grid-column: 1 / span 2;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  /*background:linear-gradient(to bottom, var(--tertiary-color1), white)*/
}

.active-anim{
  display: flex;
  position: absolute;
  height: 100%;
  width: 100%;
  justify-content: center;
  align-items: center;
}

.not-active-anim{
  display: none;
}

.intro-vid{
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  grid-row: 2 / span 1;
  grid-column: 1 / span 1;
  height: 100%;
  width: 100%;
}

.minvid, .minviduten{
  position: absolute;
  width: 85%;
  height: 80%;
  display: flex;
  align-items: center;
  justify-content: center;
  object-fit: cover;
  border-radius: 20px;
  box-shadow: 0 0 20px 4px var(--tertiary-color1);
}

.minvid::-webkit-media-controls{
  background-color:#0095ff5a;
}

.minviduten::-webkit-media-controls{
  background-color: none;
}

/* Example overlay styling */
.intro-vid .spill {
  height: 100%;
  width: 100%;
  z-index: 3;
  display: flex;
  align-items: center;
  justify-content: center;
}
.video-tittel{
  font-size: 1.2em;
  font-weight: 500;
  color: var(--secondary-color3);
}


.calc-div{
  position: absolute;
  grid-row: 2 / span 1;
  grid-column: 2 / span 1;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.calc-link{
  position: absolute;
  width: 85%;
  height: 80%;
  border-radius: 20px;
  background-color: var(--primary-color1);
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  animation: animer-calc1 300ms 1;
  box-shadow: 0 0 20px 4px var(--tertiary-color1);
}

@keyframes animer-calc1 {
  0%{
    width: 90%;
    height: 85%;
  }
  100%{
    width: 85%;
    height: 80%;
  }
}

.calc-link:hover{
  width: 90%;
  height: 85%;
  cursor: pointer;
  animation: animer-calc2 300ms 1;
}

@keyframes animer-calc2 {
  0%{
    width: 85%;
    height: 80%;
  }
  100%{
    width: 90%;
    height: 85%;
  }
}

.img-div{
  grid-column: 1 / span 1;
  position: absolute;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  background-image:url("../bilder/calc-img2.jpg");
  background-size: cover;
}

.calc-link-info{
  grid-column: 2 / span 1;
  position: absolute;
  height: 100%;
  width: 100%;
}


.tittel-div{
  position: absolute;
  height: 15%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.sub-info-div{
  position: absolute;
  top: 15%;
  height: 85%;
  width: 100%;
  display: grid;
  grid-template-rows: repeat(5, 1fr);
}

.calc-sub-info{
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.tittel{
  font-size: 1.1em;
  font-weight: 5400;
  color: var(--secondary-color3);
  margin-left: 5px;
}

.calc-text{
  font-size: 1em;
  font-weight: 500;
  color: var(--primary-color3);
  margin-left: 5px;
}























/**animation**/

/*math animatin*/
.math-anim-svg{
  display: block;
  width: 100%;
  height: 265px;
}

.l2{
  stroke: #ffffff00;
  stroke-width: 3;
  animation: graf-xakse 8000ms 1;
}

@keyframes graf-xakse {
  0%{
    stroke: var(--secondary-color3);
    transform: scaleX(0.1);
  }
  25%{
    transform: scaleX(1);
    stroke: var(--secondary-color3);
  }

  99%{
    stroke: var(--secondary-color3);
  }
}

.l1{
  stroke: #ffffff00;
  stroke-width: 3;
  animation: graf-yakse 8000ms 1;
}

@keyframes graf-yakse {
  0%{
    transform: scaleY(0.1);
    stroke: var(--secondary-color3);
  }
  7%{
    transform: scaleY(1);
    stroke: var(--secondary-color3);
  }

  99%{
    stroke: var(--secondary-color3);
  }
}

.graf{
  stroke: #ffffff00;
  stroke-width: 3;
  animation: graf-anim 8000ms 1;
}

@keyframes graf-anim {
  14%{
    stroke: #ffffff00;
  }

  15%{
    transform: translateY(-50vh);
    stroke: var(--secondary-color3);
  }
  
  22%{
    transform: translateY(0vh);
    stroke: var(--secondary-color3);
  }

  99%{
    transform: translateY(0vh);
    stroke: var(--secondary-color3);
  }
}

.integral{
  fill: #ffffff00;
  stroke: #ffffff00;
  animation: integral-anim 8000ms 1;
}

@keyframes integral-anim {
  30%{
    fill: #ffffff00;
    stroke: #ffffff00;
  }
  37%{
    fill: var(--primary-color3);
    stroke: var(--primary-color3);
  }

  99%{
    fill: var(--primary-color3);
    stroke: var(--primary-color3);
  }
}

.deriv{
  fill: #ffffff00;
  stroke: #ffffff00;
  animation: deriv-anim 8000ms 1;
}

@keyframes deriv-anim {
  44%{
    fill: #ffffff00;
    stroke: #ffffff00;
  }
  52%{
    fill: var(--primary-color3);
    stroke: var(--primary-color3);
  }

  99%{
    fill: var(--primary-color3);
    stroke: var(--primary-color3);
  }
}


