/* skrift anim*/

.skrift-anim{
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.skrift-anim-text{
  font-size: 2.5em;
  font-weight: 500;
  width: 100%;
  text-align: center;
}

.watch-skrift{
  color: #ffffff00;
  text-shadow: #ffffff00 0px 0px 0px;
  animation: watch-anim 10000ms 1;
}

@keyframes watch-anim {
  0%{
    transform: translateX(-100vw);
    color: var(--primary-color3);
    text-shadow: var(--primary-color3) 0px 0px 40px;
  }
  5%{
    transform: translateX(0vw);
    color: var(--primary-color3);
    text-shadow: var(--primary-color3) 0px 0px 40px;
  }
  28%{
    transform: translateX(0vw);
    color: var(--primary-color3);
    text-shadow: var(--primary-color3) 0px 0px 40px;
  }
  33%{
    transform: translateX(100vw);
    color: var(--primary-color3);
    text-shadow: var(--primary-color3) 0px 0px 40px;
  }
  34%{
    color: #ffffff00;
    text-shadow: #ffffff00 0px 0px 0px;
  }
}

.problems-skrift{
  color: #ffffff00;
  text-shadow: #ffffff00 0px 0px 0px;
  animation: problem-anim 10000ms 1;
}

@keyframes problem-anim {
  32%{
    transform: translateX(-100vw);
    color: #ffffff00;
    text-shadow: #ffffff00 0px 0px 0px;
  }
  33%{
    transform: translateX(-100vw);
    color: var(--primary-color3);
    text-shadow: var(--primary-color3) 0px 0px 40px;
  }
  38%{
    transform: translateX(0vw);
    color: var(--primary-color3);
    text-shadow: var(--primary-color3) 0px 0px 40px;
  }
  61%{
    transform: translateX(0vw);
    color: var(--primary-color3);
    text-shadow: var(--primary-color3) 0px 0px 40px;
  }
  66%{
    transform: translateX(100vw);
    color: var(--primary-color3);
    text-shadow: var(--primary-color3) 0px 0px 40px;
  }
  67%{
    color: #ffffff00;
    text-shadow: #ffffff00 0px 0px 0px;
  }
}

.answer-skrift{
  color: #ffffff00;
  text-shadow: #ffffff00 0px 0px 0px;
  animation: answer-anim 10000ms 1;
}

@keyframes answer-anim {
  65%{
    transform: translateX(-100vw);
    color: #ffffff00;
    text-shadow: #ffffff00 0px 0px 0px;
  }
  66%{
    transform: translateX(-100vw);
    color: var(--primary-color3);
    text-shadow: var(--primary-color3) 0px 0px 40px;
  }
  71%{
    transform: translateX(0vw);
    color: var(--primary-color3);
    text-shadow: var(--primary-color3) 0px 0px 40px;
  }
  94%{
    transform: translateX(0vw);
    color: var(--primary-color3);
    text-shadow: var(--primary-color3) 0px 0px 40px;
  }
  99%{
    transform: translateX(100vw);
    color: var(--primary-color3);
    text-shadow: var(--primary-color3) 0px 0px 40px;
  }
  100%{
    transform: translateX(100vw);
    color: #ffffff00;
    text-shadow: #ffffff00 0px 0px 0px;
  }
}












/*courses section*/
